body {
    background-color: black;
    font-family: 'Roboto', sans-serif;
}

#app {
    background-color: #2b2b2b50;
    height: 100vh;
    width: 1000px;
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    display: grid;
    grid-template-rows: 1fr 15fr 1fr;
    font-family: 'Roboto', sans-serif;
}

.Preview {
    margin: 10px;
    background-color: #2b2b2b50;
    overflow-y: scroll;
    padding-left: 10px;
    padding-right: 10px;
    font-family: 'Roboto', sans-serif;
}

.Option_Preview {
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    margin-bottom: 5px;
    margin-top: 5px;
    border: dashed 1px #2b2b2b50;
    padding: 5px;
    font-family: 'Roboto', sans-serif;
}

.Option_Preview:hover {
    border: dashed 1px #4CAF50;
    font-family: 'Roboto', sans-serif;
}

.btn {
    -webkit-appearance: none;
    width: 100%;
    height: 20px;
    background: #2b2b2b50;
    outline: none;
    -webkit-transition: .2s;
    -webkit-transition: opacity .2s;
    transition: opacity .2s;
    width: 300px;
    font-family: 'Roboto', sans-serif;
  }

.btn::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 50px;
    height: 20px;
    background: #4CAF50;
    cursor: pointer;
    font-family: 'Roboto', sans-serif;
  }

.btn::-moz-range-thumb {
    width: 25px;
    height: 25px;
    background: #4CAF50;
    cursor: pointer;
    font-family: 'Roboto', sans-serif;
  }

.option {
      font-size: 20px;
      font-family: 'Roboto', sans-serif;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
  }

/* width */

::-webkit-scrollbar {
    width: 20px;
  }

/* Track */

::-webkit-scrollbar-track {
    background: #2b2b2b50;
  }

/* Handle */

::-webkit-scrollbar-thumb {
    background: #4CAF50;
  }

/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
    background: #4CAF50;
  }

.getConfig {
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
    background-color: #2b2b2b50;
    color: white;
    outline: none;
    cursor: pointer;
    font-size: 20px;
    border: solid #2b2b2b50 1px;
    font-family: 'Roboto', sans-serif;
  }

.getConfig:hover {
    border: solid #4CAF50 1px;
    color: #4CAF50;
    font-family: 'Roboto', sans-serif;
  }

.info {
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 10px;
    background-color: #2b2b2b50;
    color: white;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size: 20px;
    border: solid #2b2b2b50 1px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    font-family: 'Roboto', sans-serif;
  }

.fas {
      margin-right: 5px;
      color: #4CAF50;
  }